
.homeTransition-enter-active,.homeTransition-leave-active{
	transition:opacity 0.5s;
}
.homeTransition-enter,.homeTransition-leave-active{
	opacity: 0;
}
.ZhihuHeadFixClass-none{
	position: fixed;
	z-index: 2;
	top:-2rem;
	width: 100%;
	transition:all 0.5s ease;
}
.ZhihuHeadFixClass-on{
	position: fixed;
	top:0rem;
	width: 100%;
	transition:all 0.5s ease;
	z-index: 2;
}
.news-latest-time{
	border-left: 5px solid #efefef;
	font-size: 0.8rem;
	color:grey;
	padding: 0.1rem;
	text-indent: 0.5rem;
	animation:newsLatestTime 0.5s ease 1;
}
@keyframes newsLatestTime{
from{
		transform:translateX(100px);
		opacity: 0;
}
to{
		transform:translateX(0);
		opacity: 1;
}
}
.load-more-button{
	border-style: none;
	outline: none;
	background-color: white;
	border-bottom:1px solid #efefef;
	width: 100%;
	font-size: 0.8rem;
}
.back-to-top{
	background-color: white;
	position: fixed;
	bottom: 1rem;
	right: 1rem;
	outline:none;
	border-style: none;
}



