
.loading-two[data-v-08cdbe1a]{
	width: 100%;
	height: 100vh;
	/*窗口高度的100%*/
	padding-top:25%;
	background-color: white;
}
.loading-two p[data-v-08cdbe1a]{
	text-align: center;
}
.double-flash[data-v-08cdbe1a]{
	animation:double-flash 3s ease infinite;
	width: 50px;
	margin-left: 50%;
	transform:translateX(-50%)
}
@keyframes double-flash{
0%{
		opacity: 1;
}
12.5%{
		opacity: 0;
}
25%{
		opacity: 1;
}
37.5%{
		opacity: 0;
}
50%{
		opacity: 1;
}
100%{
		opacity: 1;
}
}



